@import url('https://fonts.googleapis.com/css2?family=Lexend+Deca:wght@100..900&display=swap');

:root {
  --accent-color-1: #73877b;
  --accent-color-2: #839788;
  --progress-green: #4ade80;
  --progress-orange: #fb923c;
  --progress-red: #f87171;
  --progress-bg: #e5e7eb;
  /* Custom viewport height for mobile keyboard handling */
  --vh: 1vh;
}
.App {
  font-family: Lexend Deca, sans-serif;
  text-align: center;
  max-width: 390px;
  overflow-x: hidden;
  margin: 10px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.add-button {
  position: fixed;
  bottom: 20px; /* Adjust distance from the bottom */
  right: 20px; /* Adjust distance from the right */
  z-index: 999; /* Ensure it stays on top of other elements */
  
  /* Button styling */
  background-color: var(--progress-orange);
  color: white;
  border: none;
  border-radius: 50%;
  width: 56px;
  height: 56px;
  font-size: 24px;
  /* box-shadow: 0 4px 8px rgba(0,0,0,0.2); */
  box-shadow: rgb(173, 104, 47) -5px 5px;
}

.modal-input-container > span {
  padding: 10px;
  font-size: 1.1rem;
  font-weight: 500;
  color: #333;
  text-align: left;
  width: 100%;
  display: block;
}

.card {
  border: 2px solid var(--accent-color-1);
  border-radius: 15px;
  width: 170px;
  height: 180px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  /* box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px; */
  box-shadow: var(--accent-color-2) 5px;
  cursor: pointer;
  transition: all 0.3s ease;
  background-color: #ffffff;
  padding: 14px;
}

.card:hover {
  transform: translateY(-2px);
  box-shadow: var(--accent-color-2) -10px 10px;
  border-color: var(--accent-color-2);
}

.card:active {
  transform: translateY(0);
  box-shadow: var(--accent-color-2) -5px 5px;
}

.card-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  gap: 10px;
}

.card-title {
  font-size: 1.5rem;
  font-weight: 500;
  color: #333;
  width: 100%;
  /* text-align: left; */
  /* margin-bottom: 10px; */
  line-height: 1.3;
  display: flex;
  justify-content: space-between;
}

.card-details {
  font-size: 1.4rem;
  font-weight: 700;
  color: var(--accent-color-1);
}

/* Progress Bar Styles */
.progress-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.progress-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.85rem;
  color: #666;
  /* margin-bottom: 6px; */
  font-weight: 400;
}

.progress-text {
  font-size: 0.75rem;
}

.progress-bar {
  width: 100%;
  height: 10px;
  background-color: var(--progress-bg);
  border-radius: 4px;
  overflow: hidden;
  position: relative;
}

.progress-fill {
  height: 100%;
  transition: width 0.3s ease, background-color 0.3s ease;
  border-radius: 4px;
  position: relative;
}

.progress-fill::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, transparent 0%, rgba(255,255,255,0.2) 50%, transparent 100%);
  animation: shimmer 2s infinite;
}

.remaining-amount {
  font-size: 0.8rem;
  color: #888;
  text-align: center;
  margin-top: 4px;
}

@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

.card-container {
  display: flex;
  flex-direction: row;
  gap: 10px;
  align-items: center;
  padding-top: max(60px, env(safe-area-inset-top));
  padding-bottom: max(60px, env(safe-area-inset-bottom));
  padding-left: max(20px, env(safe-area-inset-left));
  padding-right: max(20px, env(safe-area-inset-right));
}

.App-header {
  /* background: linear-gradient(135deg, #667eea 0%, #764ba2 100%); */
  padding: 60px 20px;
  color: white;
  min-height: 100vh;
  min-height: 100dvh; /* Dynamic viewport height for mobile */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* Safe area support for modern iPhones */
  padding-top: max(60px, env(safe-area-inset-top));
  padding-bottom: max(60px, env(safe-area-inset-bottom));
  padding-left: max(20px, env(safe-area-inset-left));
  padding-right: max(20px, env(safe-area-inset-right));
}

.App-header h1 {
  font-size: clamp(2rem, 8vw, 3rem);
  margin-bottom: 1rem;
  font-weight: 300;
  line-height: 1.2;
}

.App-header p {
  font-size: clamp(1rem, 4vw, 1.2rem);
  margin-bottom: 1rem;
  max-width: min(600px, 90vw);
  line-height: 1.6;
}

.links {
  margin-top: 2rem;
  display: flex;
  gap: 2rem;
  flex-wrap: wrap;
  justify-content: center;
}

/* iPhone 16 and similar devices (393px viewport) */
@media (max-width: 500px) {
  .App-header {
    padding: 40px 16px;
    padding-top: max(40px, env(safe-area-inset-top));
    padding-bottom: max(40px, env(safe-area-inset-bottom));
  }
  
  .App-header h1 {
    font-size: 1.8rem;
    margin-bottom: 0.8rem;
  }
  
  .App-header p {
    font-size: 0.95rem;
    margin-bottom: 0.8rem;
    max-width: 95vw;
  }
  
  .links {
    flex-direction: column;
    gap: 1rem;
    margin-top: 1.5rem;
    width: 100%;
    max-width: 280px;
  }
  
  .App-link {
    width: 100%;
    padding: 14px 20px;
    font-size: 0.95rem;
  }
  
  /* Mobile card adjustments */
  .card {
    width: 100%;
    height: 115px;
    padding: 12px;
  }
  
  .card-title {
    font-size: 1.4rem;
    /* margin-bottom: 5px; */
    /* text-align: left; */
  }
  
  .progress-info {
    font-size: 0.8rem;
    /* margin-bottom: 5px; */
  }
  
  .progress-percentage {
    font-size: 1.2rem;
    font-weight: 400;
  }
  
  .progress-bar {
    height: 8px;
  }
  
  .remaining-amount {
    font-size: 0.75rem;
    margin-top: 3px;
  }
  
  .card-container {
    gap: 25px;
    flex-wrap: wrap;
    justify-content: center;
  }
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100); /* Custom property fallback */
  height: 100dvh; /* Dynamic viewport height for mobile keyboards */
  min-height: 100vh; /* Fallback for browsers that don't support dvh */
  min-height: 100dvh;
  min-height: -webkit-fill-available; /* iOS Safari specific */
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 20px;
  backdrop-filter: blur(4px);
  overflow: hidden;
  /* Ensure modal covers the entire screen including when keyboard is visible */
  -webkit-overflow-scrolling: touch;
  /* Force hardware acceleration for better performance */
  will-change: transform;
  transform: translateZ(0);
}

.modal-content {
  background: white;
  border-radius: 15px;
  max-width: 400px;
  width: 100%;
  max-height: 90vh;
  max-height: 90dvh; /* Dynamic viewport height for mobile */
  overflow-y: auto;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  animation: modalSlideIn 0.3s ease-out;
  position: relative;
  /* Ensure modal content is properly positioned */
  margin: auto;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  
  align-items: center;
  padding: 20px 20px 10px 20px;
  /* border-bottom: 1px solid #e0e0e0; */
}

.modal-title {
  margin: 0;
  font-size: 1.6rem;
  color: #333;
  font-weight: 500;
  line-height: 1.3;
}

.modal-close {
  background: none;
  border: none;
  font-size: 2rem;
  color: #666;
  cursor: pointer;
  padding: 6px;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.modal-close:hover {
  background-color: #f0f0f0;
  color: #333;
}

.modal-body {
  padding: 10px;
}

.modal-input {
  width: 100%;
  padding: 16px 18px;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  font-size: 1.1rem;
  transition: border-color 0.2s ease;
  box-sizing: border-box;
  -webkit-appearance: none;
  appearance: none;
}

.modal-input:focus {
  outline: none;
  border-color: var(--accent-color-1);
  box-shadow: 0 0 0 3px rgba(115, 135, 123, 0.1);
}

.modal-footer {
  padding: 10px 20px 20px 20px;
  display: flex;
}

.modal-button {
  background-color: var(--accent-color-1);
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  min-height: 44px;
  width: 100%;
}

.modal-button:hover {
  background-color: var(--accent-color-2);
  transform: translateY(-1px);
}

.modal-button:active {
  transform: translateY(0);
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-50px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* Mobile-specific modal adjustments */
@media (max-width: 430px) {
  .modal-overlay {
    padding: 10px;
    /* Ensure overlay always covers full screen on mobile */
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100vw;
    height: 100vh;
    height: 100dvh;
    min-height: -webkit-fill-available; /* iOS Safari fix */
  }
  
  .modal-content {
    border-radius: 8px;
    max-height: 85vh;
    max-height: 85dvh;
    /* Better mobile positioning when keyboard is visible */
    margin: auto 0;
    align-self: center;
  }
  
  .modal-header {
    padding: 15px 15px 10px 15px;
  }
  
  .modal-title {
    font-size: 1.4rem;
  }
  
  .modal-body {
    padding: 15px;
  }
  
  .modal-footer {
    padding: 10px 15px 15px 15px;
  }
  
  .modal-button {
    width: 100%;
    padding: 18px 24px;
    font-size: 1.2rem;
  }
}

/* Swipeable Card Styles */
.swipeable-card-container {
  position: relative;
  width: 100%;
  height: auto;
  overflow: hidden;
  border-radius: 15px;
}

.delete-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #ef4444, #dc2626);
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-left: 20px;
  border-radius: 15px;
  z-index: 1;
}

.refresh-background {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #10b981, #059669);
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 20px;
  border-radius: 15px;
  z-index: 1;
}

.delete-icon,
.refresh-icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  font-weight: 600;
}

.delete-icon span:first-child,
.refresh-icon span:first-child {
  font-size: 1.5rem;
  margin-bottom: 4px;
}

.delete-text,
.refresh-text {
  font-size: 0.8rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.swipeable-card {
  position: relative;
  z-index: 2;
  background: white;
  border-radius: 15px;
  will-change: transform;
  user-select: none;
  -webkit-user-select: none;
  -webkit-touch-callout: none;
}

.swipeable-card.dragging {
  cursor: grabbing;
  transition: none !important;
}

.swipeable-card.deleting {
  opacity: 0.8;
  transform: translateX(-300px) !important;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1) !important;
}

.swipeable-card.refreshing {
  opacity: 0.9;
  transform: translateX(300px) !important;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1) !important;
}

.swipe-indicator {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: white;
  padding: 8px 12px;
  border-radius: 20px;
  font-size: 0.75rem;
  font-weight: 500;
  z-index: 3;
  animation: swipePulse 1s ease-in-out infinite alternate;
  pointer-events: none;
}

.swipe-indicator.left {
  right: 20px;
  background: rgba(239, 68, 68, 0.9);
}

.swipe-indicator.right {
  left: 20px;
  background: rgba(16, 185, 129, 0.9);
}

@keyframes swipePulse {
  0% { opacity: 0.7; transform: translateY(-50%) scale(0.95); }
  100% { opacity: 1; transform: translateY(-50%) scale(1); }
}

/* Mobile optimizations for swipe */
@media (max-width: 430px) {
  .swipeable-card-container {
    margin-bottom: 4px;
  }
  
  .delete-background {
    padding-left: 15px;
  }
  
  .refresh-background {
    padding-right: 15px;
  }
  
  .delete-icon span:first-child,
  .refresh-icon span:first-child {
    font-size: 1.3rem;
  }
  
  .delete-text,
  .refresh-text {
    font-size: 0.7rem;
  }
  
  .swipe-indicator.left {
    right: 15px;
    padding: 6px 10px;
    font-size: 0.7rem;
  }
  
  .swipe-indicator.right {
    left: 15px;
    padding: 6px 10px;
    font-size: 0.7rem;
  }
}
